.header-container   {
  position: relative;
  margin-left: 200px;
  margin-right: 100px;
  height: 60px;
  
}

.title-item {
  position: absolute;
}

h1 {
   color: darkkhaki;
}
.input-item {
  position: absolute;
  margin-top: 30px;
  margin-left: 250px;
  

}

label {
  padding-left: 20px;

}
 
.results-container {
  display: grid;
  margin-top: 25px;
  margin-left: 350px;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  width: 500px;
}

.result-image {
  
   grid-row: 1/2;
   margin-top: 2px;
   width: 100px;
   
}

.result-image img {
  width: 100px;
}

.result-info {
  grid-row: 1/2;
  margin-left: 5px;
  width: 300px;
}